import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs';
import { filter, map, tap } from 'rxjs/operators';
import { data } from '../portfolio-page/portfolio-data.mock';
import { PortfolioItem } from '../portfolio-page/portfolio-item.interface';

@Component({
  selector: 'app-portfolio-item',
  templateUrl: './portfolio-item.component.html',
  styleUrls: ['./portfolio-item.component.scss']
})
export class PortfolioItemComponent implements OnInit {
  data: PortfolioItem[] = data;
  data$: Observable<PortfolioItem>;
  gridColumns: number;
  gridRows: number;

  constructor(
    private route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.getData();
  }

  getData(): void {
    this.data$ = this.route.paramMap.pipe(
      map(params => params.get('slug')),
      map(slug => this.data.find(item => item.slug === slug)),
      tap(item => {
        if (!(item.gallery && item.gallery.length)) {
          return;
        }

        this.gridColumns = Math.max.apply(Math, [...item.gallery].map(gItem => gItem.data.column_end)) - 1;
        this.gridRows = Math.max.apply(Math, [...item.gallery].map(gItem => gItem.data.row_end)) - 1;
      })
    );
  }

}
