import { AfterViewInit, Component, OnDestroy, OnInit } from '@angular/core';
import { fromEvent, Subject } from 'rxjs';
import { debounceTime, filter, map, takeUntil, tap } from 'rxjs/operators';
import { data } from './portfolio-data.mock';
import { PortfolioItem } from './portfolio-item.interface';

@Component({
  selector: 'app-portfolio-page',
  templateUrl: './portfolio-page.component.html',
  styleUrls: ['./portfolio-page.component.scss']
})
export class PortfolioPageComponent implements OnInit, AfterViewInit, OnDestroy {
  pages: PortfolioItem[] = [...data, ...data, ...data, ...data, ...data, ...data, ...data, ...data, ...data];
  activeGallery = 0;
  windowWidth = window.innerWidth;
  limitDivider = this.getLimitDividerBasingOnScreenWidth();
  activeGalleryLimit = Math.floor(this.pages.length / this.limitDivider);
  galleryHeight;
  destroy$: Subject<void> = new Subject();

  constructor() { }

  ngOnInit(): void {
    this.listenForWindowResize();
    this.listenForScroll();
  }

  ngAfterViewInit(): void {
    setTimeout(() => {
      this.calculateGalleryHeight();
    }, 0);
  }

  listenForWindowResize(): void {
    fromEvent(window, 'resize').pipe(
      takeUntil(this.destroy$),
      debounceTime(100),
      tap(() =>  {
        this.windowWidth = window.innerWidth;
        this.limitDivider = this.getLimitDividerBasingOnScreenWidth();
        this.activeGalleryLimit = Math.floor(this.pages.length / this.limitDivider);
        this.calculateGalleryHeight();

        if (this.windowWidth <= 768) {
          this.activeGallery = 0;
        }
      })
    ).subscribe();
  }

  listenForScroll(): void {
    fromEvent(document, 'wheel').pipe(
      takeUntil(this.destroy$),
      filter(() => window.innerWidth > 768),
      debounceTime(30),
      map((wheelEvent: WheelEvent) => wheelEvent.deltaY > 0),
      filter(isDownScroll => {
        return isDownScroll && (this.activeGallery + 1) < this.activeGalleryLimit || !isDownScroll && this.activeGallery >= 0;
      }),
      tap((isDownScroll: boolean) => {
        isDownScroll ? this.activeGallery++ : this.activeGallery--;
      })
    ).subscribe();
  }

  nextGallery(): void {
    this.activeGallery++;
  }

  previousGallery(): void {
    this.activeGallery--;
  }

  calculateGalleryHeight(): void {
    this.galleryHeight = ((document.getElementsByClassName('portfolio-item')[0] as HTMLElement).offsetHeight + 30) * 2;
  }

  getLimitDividerBasingOnScreenWidth(): number {
    const width = window.innerWidth;
    return (width < 768 && 1) || (width < 1360 && 4) || 6;
  }

  ngOnDestroy(): void {
    this.destroy$.next();
  }
}
