import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ContactPageComponent } from './components/contact-page/contact-page.component';
import { MainPageComponent } from './components/main-page/main-page.component';
import { PortfolioItemComponent } from './components/portfolio-item/portfolio-item.component';
import { PortfolioPageComponent } from './components/portfolio-page/portfolio-page.component';


const routes: Routes = [
  { path: '', component: MainPageComponent, data: { animation: 'Home' } },
  { path: 'portfolio', component: PortfolioPageComponent, data: { animation: 'Portfolio' } },
  { path: 'portfolio/:slug', component: PortfolioItemComponent, data: { animation: 'PortfolioItem' } },
  { path: 'kontakt', component: ContactPageComponent, data: { animation: 'Contact' } }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { relativeLinkResolution: 'legacy' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
