import {
  transition,
  trigger,
  query,
  style,
  animate,
  group
} from '@angular/animations';

export const routeAnimation =
  trigger('routeAnimation', [
    transition('* <=> *', [
      query(':enter, :leave',
        style({ position: 'fixed', width: '100%', 'background-color': 'transparent' }),
        { optional: true }),
      group([
        query(':enter', [
          style({ opacity: 0 }),
          animate('0.3s ease-in-out',
            style({ opacity: 1 }))
        ], { optional: true }),
        query(':leave', [
          style({ opacity: 1 }),
          animate('0.3s ease-in-out',
            style({ opacity: 0 }))
        ], { optional: true }),
      ])
    ])
  ]);
